function toggleMenu() {
    const showMenuBtn = document.querySelector(".new-header__show-menu");
    const closeMenuBtn = document.querySelector(".new-header__close-menu");
    const nav = document.querySelector(".new-nav");

    showMenuBtn.addEventListener('click', function () {
        this.classList.add('hide');
        closeMenuBtn.classList.add('show');
        nav.classList.add('show');
        hideScroll();
    });

    closeMenuBtn.addEventListener('click', function () {
        showMenuBtn.classList.remove('hide');
        this.classList.remove('show');
        nav.classList.remove('show');
        showScroll();
    });
}

toggleMenu();